import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Atividade } from 'src/app/models/atividade.interface';
import { Operacao } from 'src/app/models/operacao.interface';
import { OperationService } from 'src/app/services/operation.service';
import { CentralDownloadService } from '../services/central-download.service';
import * as moment from 'moment';
import { CentralDownload, FileCentralDownload } from 'src/app/models/central-download.interface';
import { MatTableDataSource, PageEvent } from '@angular/material';

@Component({
	selector: 'app-central-downloads',
	templateUrl: './central-downloads.component.html',
	styleUrls: ['./central-downloads.component.scss']
})
export class CentralDownloadsComponent implements OnInit {

	public loading: boolean;
	public showMessage: boolean;
	public formSearch: FormGroup;
	public operations: Operacao[] = [];
	public activities: Atividade[] = [];
	public currentData: Date = new Date();

	/** Table */
	public dataSource = new MatTableDataSource<CentralDownload>();
	public displayedColumns: string[] = ['id', 'tipo', 'produto', 'status', 'dataSolicitacao', 'dataConclusao', 'actions'];

	/** Paginator */
	public length: number = null;
	public pageSize: number = 5;
	public pageIndex: number = 0;

	constructor(
		private formBuilder: FormBuilder,
		private operationService: OperationService,
		private centralDownloadService: CentralDownloadService,
	) {}
	
	ngOnInit() {
		this.formSearch = this.formBuilder.group({
			type : [''],
			operation : [''],
			activity : [''],
			status : [''],
			initialDate : ['', [Validators.required]],
			finalDate : ['', [Validators.required]],
		})

		this.formSearch.get('activity').disable();

		/** Lista de Operações sem Template Associado */
		this.operationService.getOperations().subscribe(
			response => {
				this.operations = [...response.data];
			},
			() => {
				// Mensagem de Erro para Serviço Indisponível. Não tem como prosseguir
			}
		);
	}

	public setOperation(): void {
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.formSearch.value.operation);
		this.activities = [...operation.atividades];
		this.formSearch.get('activity').enable();
	}

	public search(): void {
		if (this.formSearch.valid) {
			const form: any = this.formSearch.value;
			const initialDate: string = moment(form.initialDate).format('YYYY-MM-DD');
			const finalDate: string = moment(form.finalDate).format('YYYY-MM-DD');
			const activity: string = form.activity || '';
	
			this.centralDownloadService.getListDownloads(
				activity, form.status, form.type, initialDate, finalDate, (this.pageIndex + 1).toString(), this.pageSize.toString()
			).subscribe(
				response => {
					this.loading = false;
					this.dataSource = new MatTableDataSource<CentralDownload>(response.data);
					this.length = response.length;
				}
			)
		} else {
			this.formSearch.markAllAsTouched();
		}
	}

	public changePaginator(event: PageEvent): void {
		this.pageIndex = event.pageIndex;
		this.pageSize = event.pageSize;
		this.search();
	}

	public getDownload(file: FileCentralDownload): void {
		this.centralDownloadService.getFile(file.url).subscribe(
			response => {
				const blob = new Blob([response.body], { type: 'application/octet-stream' });
				const linkArquivo = window.URL.createObjectURL(blob);
				const fileName = response.headers.get('content-disposition').split(';')[1].split('filename')[1].split('=')[1].trim();
				var link = document.createElement("a");
				link.setAttribute("href", linkArquivo.toString());
				link.setAttribute("download", fileName);
				link.style.display = "none";
				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);
			}
		)
	}
	
}
