import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { ResponseList } from 'src/app/models/response-list.interface';

@Injectable({
	providedIn: 'root'
})
export class CentralDownloadService {

    constructor (
        private http: HttpClient,
    ) {}

    public getListDownloads (
        atividadeId: string, 
        statusId: string, 
        tipoId: string, 
        dataInicial: string, 
        dataFinal: string, 
        pageIndex: string, 
        pageSize: string, 
    ): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/central-downloads`, {
            params : {
                AtividadeId : atividadeId,
                StatusId : statusId,
                TipoId : tipoId,
                DataSolicitacao : dataInicial,
                DataConclusao : dataFinal,
                PageSize : pageSize,
                PageIndex : pageIndex,
            }
        })
    }

    public getFile(link: string): Observable<any> {
        return this.http.get<any>(link, {
            responseType : 'blob' as 'json',
            observe : 'response'
        });
    }
}